import "./forgotpassword.css";
import "./signup.css";
import { Link } from "react-router-dom";

function Forgot() {
    return <>
        <div class="form-gap wrapper fadeInDown"></div>
        <div class="container fadeInDown">
            <div class="row">
                <div class="col-md-4 col-md-offset-4">
                    <div class="panel panel-default">
                        <div class="panel-body">
                            <div class="text-center">
                                <h3><i class="fa fa-lock fa-4x"></i></h3>
                                <h2 class="text-center">Forgot Password?</h2>
                                <p>You can reset your password here.</p>
                                <div class="panel-body formContent">

                                    <form id="register-form" autocomplete="off" class="form" method="post">
                                        <input type="text" id="login" class="fadeIn second" name="email" placeholder="email" />
                                        <div class="form-group">
                                            <input type="submit" class="fadeIn fourth"/>
                                        </div>
                                        <div id="formFooter">
                                            <Link class="underlineHover" to="/signup">Signup Here?</Link>
                                            <Link class="underlineHover" to="/">Login Here</Link>
                                        </div>
                                    </form>


                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </>
}

export default Forgot;