import axios from "axios";

export function PostUserData(data){
    return axios.post('https://url-shortner-nodes.herokuapp.com/createuser/',data);
}

export function GetUserData(){
    return axios.get('https://url-shortner-nodes.herokuapp.com/login');
}

export function UrlData(urldata){
    return axios.post('https://url-shortner-nodes.herokuapp.com/url',urldata)
}

export function GetShortUrl(){
    return axios.get('https://url-shortner-nodes.herokuapp.com/url');
}

export function Random(){
    return axios.get('https://url-shortner-nodes.herokuapp.com/random');
}