import React, { useState, useEffect } from 'react';
import { GetUserData } from "./api";
import './login.css';
import { Link, useHistory } from "react-router-dom";

function Login() {
    let [userData, setuserData] = useState([])
    let [usercheckEmail, setusercheckEmail] = useState()
    let [usercheckPassword, setusercheckPassword] = useState()
    useEffect(async () => {
        let users = await GetUserData();
        // console.log(users.data)
        setuserData(...userData, users.data)
        // console.log(users.data)
    }, [])
        let history = useHistory();
 
    return <>
        <div class="wrapper fadeInDown">
            <div id="formContent">

                <div class="fadeIn first">
                    <h3 class="underlineHover btn-primary">Login</h3>
                </div>


                <form onSubmit={async (e) => {
                    e.preventDefault();
                    // console.log(userData);
                    {
                        userData.map((users) => {
                            if (usercheckEmail == users.email && usercheckPassword == users.pass) {
                                // console.log(users)
                                history.push(`/user/${users.email}`);
                                // window.open(`http://localhost:3000/user/${users.email}`, "_self");
                            }
                            else {
                                console.log("NO USER FOUND")
                            }
                        })
                    }
                    // users.setuserList([...users.userList, userData])
                    // await PostUserData(userData)
                    // console.log(usercheckEmail)
                    // console.log(usercheckPassword)
                }}>
                    <input type="text" id="email" class="fadeIn second" name="email" placeholder="email" onChange={(e) => {
                        setusercheckEmail(e.target.value)
                    }} />
                    <input type="text" id="password" class="fadeIn third" name="login" placeholder="password" onChange={(e) => {
                        setusercheckPassword(e.target.value)
                    }} />
                    <input type="submit" class="fadeIn fourth" value="Log In" />
                </form>


                <div id="formFooter">
                    <Link class="underlineHover" to="/forgotpassword">Forgot Password?</Link>
                    <Link class="underlineHover" to="/signup">Signup Here</Link>
                </div>

            </div>
        </div>
    </>
}

export default Login;