import React, { useState } from 'react';
import { Link } from "react-router-dom";
import { PostUserData } from "./api";

function Signup() {
    let [userEmail, setuserEmail] = useState("Initial state")
    let [userFName, setuserFName] = useState("Initial state")
    let [userLName, setuserLName] = useState("Initial state")
    let [userPassword, setuserPassword] = useState("Initial state")
    let userData = {
        email: userEmail, fname: userFName, lname: userLName, pass: userPassword
    }
    return <>
        <div class="wrapper fadeInDown">
            <div id="formContent">

                <div class="fadeIn first">
                    <h3 class="underlineHover btn-primary">Sign Up</h3>
                </div>

                <form onSubmit={async (e) => {
                    e.preventDefault();
                    console.log(userData);
                    // users.setuserList([...users.userList, userData])
                    await PostUserData(userData)
                }}>
                    <input type="text" id="login" class="fadeIn second" name="email" placeholder="email" onChange={(e) => {
                        setuserEmail(e.target.value);
                    }} />
                    <input type="text" id="login" class="fadeIn second" name="fname" placeholder="first name" onChange={(e) => {
                        setuserFName(e.target.value);
                    }} />
                    <input type="text" id="login" class="fadeIn second" name="lname" placeholder="last name" onChange={(e) => {
                        setuserLName(e.target.value);
                    }} />
                    <input type="text" id="password" class="fadeIn third" name="login" placeholder="password" onChange={(e) => {
                        setuserPassword(e.target.value);
                    }} />
                    <input type="submit" class="fadeIn fourth" value="Sign Up" />
                </form>


                <div id="formFooter">
                    <Link class="underlineHover" to="/forgotpassword">Forgot Password?</Link>
                    <Link class="underlineHover" to="/">Login Here</Link>
                </div>

            </div>
        </div>
    </>
}

export default Signup;